import React, { Component, Suspense, lazy } from "react";
import { Switch, Route, Redirect } from "react-router-dom";

import Spinner from "../app/shared/Spinner";

const Dashboard = lazy(() => import("./dashboard/Dashboard"));

const Buttons = lazy(() => import("./basic-ui/Buttons"));
const DoctorDesignation = lazy(() => import("../Components/DoctorDesignation"));
const ApprovedResources = lazy(() =>
  import("../Components/FindNearby/ApprovedResources")
);
const PendingResources = lazy(() =>
  import("../Components/FindNearby/PendingResources")
);
const RejectedResources = lazy(() =>
  import("../Components/FindNearby/RejectedResources")
);

const AddDrug = lazy(() => import("../Components/DrugCalculator/AddDrug"));
const ListDrug = lazy(() => import("../Components/DrugCalculator/ListDrug"));

const Animals = lazy(() => import("../Components/Animals/AddAnimals"));
const Location = lazy(() => import("../Components/District/AddDistrict"));
const Addwhatsup = lazy(() => import("../Components/Whatsup/Addwhatsup"));

const ListFeedFormulatioin = lazy(() =>
  import("../Components/FeedFormulation/Listformulation")
);

const AddFeedFormulatioin = lazy(() =>
  import("../Components/FeedFormulation/Addformulation")
);
const ApprovedList = lazy(() => import("../Components/ApprovedList"));
const Addnewsfeed = lazy(() => import("../Components/Newsfeed/Addnewsfeed"));
const UpdateNewsfeed = lazy(() =>
  import("../Components/Newsfeed/UpdateNewsfeed")
);
const Listnewsfeed = lazy(() => import("../Components/Newsfeed/Listnewsfeed"));

const InstantDoctorList = lazy(() => import("../Components/InstantDoctorList"));
const RejectedList = lazy(() => import("../Components/RejectedList"));
const PendingList = lazy(() => import("../Components/PendingList"));
const Dropdowns = lazy(() => import("./basic-ui/Dropdowns"));
const Typography = lazy(() => import("./basic-ui/Typography"));

const BasicElements = lazy(() => import("./form-elements/BasicElements"));

const BasicTable = lazy(() => import("./tables/BasicTable"));

const Mdi = lazy(() => import("./icons/Mdi"));

const ChartJs = lazy(() => import("./charts/ChartJs"));

const Error404 = lazy(() => import("./error-pages/Error404"));
const Error500 = lazy(() => import("./error-pages/Error500"));

const Login = lazy(() => import("./user-pages/Login"));
const Register1 = lazy(() => import("./user-pages/Register"));
const Lockscreen = lazy(() => import("./user-pages/Lockscreen"));

const BlankPage = lazy(() => import("./general-pages/BlankPage"));

const token = localStorage.getItem("AuthToken");
class AppRoutes extends Component {
  render() {
    return (
      <Suspense fallback={<Spinner />}>
        <Switch>
          <Route exact path="/" component={Dashboard} />

          <Route path="/basic-ui/buttons" component={Buttons} />
          <Route
            path="/doctor/DoctorDesignation"
            component={DoctorDesignation}
          />
          <Route
            exact
            path="/Nearby/ApprovedResources"
            component={ApprovedResources}
          />
          <Route
            exact
            path="/Nearby/PendingResources"
            component={PendingResources}
          />
          <Route
            exact
            path="/Nearby/RejectedResources"
            component={RejectedResources}
          />

          <Route exact path="/Animals" component={Animals} />
          <Route path="/location" component={Location} />
          <Route path="/whatsup/Addwhatsup" component={Addwhatsup} />

          <Route path="/doctor/PendingList" component={PendingList} />
          <Route path="/Newsfeed/Addnewsfeed" component={Addnewsfeed} />
          <Route path="/Newsfeed/ListNewsfeed" component={Listnewsfeed} />
          <Route
            path="/Newsfeed/UpdateNewsfeed/:id"
            component={UpdateNewsfeed}
          />
          <Route
            path="/feed/formulation/list"
            component={ListFeedFormulatioin}
          />
          <Route path="/feed/formulation/add" component={AddFeedFormulatioin} />
          <Route path="/drug/add" component={AddDrug} />
          <Route path="/drug/list" component={ListDrug} />

          <Route path="/doctor/InstantList" component={InstantDoctorList} />
          <Route path="/doctor/ApprovedList" component={ApprovedList} />
          <Route path="/doctor/RejectedList" component={RejectedList} />
          <Route path="/basic-ui/dropdowns" component={Dropdowns} />
          <Route path="/basic-ui/typography" component={Typography} />
          <Route
            path="/form-Elements/basic-elements"
            component={BasicElements}
          />
          <Route path="/tables/basic-table" component={BasicTable} />
          <Route path="/icons/mdi" component={Mdi} />
          <Route path="/charts/chart-js" component={ChartJs} />
          {<Route path="/user-pages/login-1" component={Login} />}
          <Route path="/user-pages/register-1" component={Register1} />
          <Route path="/user-pages/lockscreen" component={Lockscreen} />
          <Route path="/error-pages/error-500" component={Error500} />
          <Route path="/general-pages/blank-page" component={BlankPage} />
          <Route path="*" component={Error404} />
        </Switch>
      </Suspense>
    );
  }
}

export default AppRoutes;
