const initialState = {};

export const userlogin = (state = initialState, actions = {}) => {
  switch (actions.type) {
    case user.login.success:
      return {
        status: true,
        payload: actions.data,
      };
    case user.login.error:
      return {
        status: false,
        payload: actions.data,
      };
    default:
      return state;
  }
};
