const doselist = localStorage.getItem("Dose");
const availablelist = localStorage.getItem("AvailableDose");

let availableList = JSON.parse(availablelist);
let doseList = JSON.parse(doselist);

const DOSE =
  doseList == null && doseList == undefined
    ? []
    : doseList.join(",").split(",");
const AVAILABLE =
  availableList == null && availableList == undefined
    ? []
    : availableList.join(",").split(",");

export function userlogin(data) {
  return (dispatch) =>
    new Promise((resolve, reject) =>
      axiosInstance("post", "/v1/admin/login", data)
        .then((res) => {
          localStorage.setItem("AuthToken", res.data.data);
          window.location.href = "/";
          dispatch({
            type: user.login.success,
            data: res.data.data,
          });
        })
        .catch((err) => {
          dispatch({
            type: user.login.error,
            data: err,
          });
          reject(err);
        })
    );
}
