export function adddose(data) {
  return (dispatch) =>
    new Promise((resolve, reject) =>
      axiosInstance("post", "/v1/admin/dose/add", data)
        .then((res) => {
          let dosee = localStorage.getItem("Dose");
          let data = JSON.parse(dosee) || [];
          data.push(res.data.data.id);
          localStorage.setItem("Dose", JSON.stringify(data));
          dosee = localStorage.getItem("Dose");
          dispatch(listdose({ id: JSON.parse(dosee).join(",").split(",") }));
          dispatch({
            type: dose.adddose.success,
            data: res.data.data,
          });
        })
        .catch((err) => {
          dispatch({
            type: dose.adddose.error,
            data: err,
          });
          reject(err);
        })
    );
}
export function listdose(data) {
  return (dispatch) =>
    new Promise((resolve, reject) =>
      axiosInstance("post", "/v1/admin/dose", data)
        .then((res) => {
          dispatch({
            type: dose.doselist.success,
            data: res.data.data,
          });
        })
        .catch((err) => {
          console.log(err.response);
          dispatch({
            type: dose.doselist.error,
            data: err,
          });
          reject(err);
        })
    );
}
export function deletedose(data) {
  return (dispatch) =>
    new Promise((resolve, reject) =>
      axiosInstance("post", "/v1/admin/dose/remove", data)
        .then((res) => {
          let dosee = JSON.parse(localStorage.getItem("Dose"));
          let deletedosee = dosee.filter((user) => {
            return user != data.id;
          });
          localStorage.setItem("Dose", JSON.stringify(deletedosee));
          dosee = localStorage.getItem("Dose");
          dispatch(listdose({ id: JSON.parse(dosee).join(",").split(",") }));
          dispatch({
            type: dose.dosedelete.success,
            data: res.data.data,
          });
        })
        .catch((err) => {
          dispatch({
            type: dose.dosedelete.error,
            data: err,
          });
          reject(err);
        })
    );
}
export function addavailbility(data) {
  return (dispatch) =>
    new Promise((resolve, reject) =>
      axiosInstance("post", "/v1/admin/availability/add", data)
        .then((res) => {
          let available = localStorage.getItem("AvailableDose");
          let availabledata = JSON.parse(available) || [];
          availabledata.push(res.data.data.id);
          localStorage.setItem("AvailableDose", JSON.stringify(availabledata));
          available = localStorage.getItem("AvailableDose");
          dispatch(
            availbilityList({ id: JSON.parse(available).join(",").split(",") })
          );
          dispatch({
            type: availability.addavailability.success,
            data: res.data.data,
          });
        })
        .catch((err) => {
          dispatch({
            type: availability.addavailability.error,
            data: err,
          });
          reject(err);
        })
    );
}
